.uspImage{
    height:50px;
    width:50px;
}

.uspBanner{
background:#e0e0e0;
}

.uspBanner.background{
    background-image: url("../../assets/images/4My-Story.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.uspTitle{
    font-family: 'FuturaCondensed' !important;
    color:#fff;
    /* padding-right:50px; */
}

.uspTitle.homeUSP{
    font-family: 'TabarroPro' !important;
    color:#000;
}

@media screen and (min-width:920px){
    .uspImage{
        height:100px;
        width:100px;
        margin-right:30px;
        margin-top:5px;
    }
}

@media screen and (max-width:910px){
    .uspTitle{
        padding-right:0px;
    }
    .uspImage{
        height:80px;
        width:80px;
        margin-right:10px;
        margin-bottom:5px;
    }
}